@import 'vars';

.btn{
    padding: 6px 12px;
    background-color: #fff;
    border: none;
    box-shadow: 0px 0px 0px 1px rgba($green, 0.75);
    &:hover{
        cursor: pointer;
    }

    &--green{
        background-color: $green;
        color: #fff;
    }
}


